@import "../../../../styles/helpers";

.control {
    display: flex;
    align-items: center; }

.button {
    flex-shrink: 0;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: $n1;
    @include d {
        background: $n3; }
    @include dark {
        background: $n7;
        @include d {
            background: $n6; } }
    svg {
        fill: $n4;
        transition: fill .2s; }
    &:hover {
        svg {
            fill: $p1; } }
    &:not(:last-child) {
        margin-right: 16px; } }


