@import "../../../../styles/helpers";

.item {
    display: flex;
    padding: 20px;
    flex: 0 0 50%;
    border-radius: 12px;
    cursor: pointer;
    transition: all .2s;
    @include d {
        display: block;
        position: relative;
        padding-left: 76px; }
    @include m {
        padding-left: 20px; } }

.icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 40px;
    height: 40px;
    margin-right: 16px;
    border-radius: 50%;
    @include d {
        position: absolute;
        top: 20px;
        left: 20px;
        margin-right: 0; }
    @include t {
        display: none; } }

.subtitle {
    display: flex;
    align-items: center;
    margin-bottom: 4px;
    @include caption1;
    color: $n4; }

.tooltip {
    svg {
        fill: $shades1; } }

.counter {
    font-size: 48px;
    font-weight: 600;
    line-height: 1;
    letter-spacing: -.03em;
    @include x {
        font-size: 40px; } }

.balance {
    margin: -12px -12px 0 auto;
    @include d {
        margin: 8px 0 0; } }
